// chat window scss
@import '~@Green-Dot-Corporation/eureka-lib-style-utils/src/styles/mixins/layout.scss';

.icon-button-chat {
    $this: &;

    &__container {
        position: fixed;
        z-index: 99999;
        border-radius: 50%;
        bottom: 25px;
        right: 25px;
    }

    &__icon-btn-chat.eureka-button {
        width: 64px;
        height: 64px;
        border-radius: 50%;
        background: var(--icon-button-chat-background-color);
        cursor: pointer;
        border: 0;
        outline: none;
        box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.25);
    }

    &__count {
        position: absolute;
        top: 0;
        right: 0;
        color: var(--icon-button-chat-unread-count-color);
        width: 12px;
        height: 12px;
        text-align: center;
        border-radius: 50%;
        background: var(--icon-button-chat-unread-count-background-color);
    }

    &__count[data-count="0"] {
        display: none;
    }

    &__count-number {
        width: 22px;
        height: 22px;
    }

    .eureka-button__icon-safe-space {
        width: auto;
    }
}
